.containerLanding{
    min-width: 100vw;
    max-width: 100vw;
    min-height: 100vh;
    max-height: 100vh;
    
    
    background-image: url('./fondo.jpg');
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;

  //Nombre-FDTX

    .container {
        width: 80vw;
        height: 75vh;
        margin: 0px;
        -webkit-perspective: 800px;
        -moz-perspective: 800px;
        -o-perspective: 800px;
        perspective: 800px;
        margin-bottom: 15vh;
      }

      .card {
        width: 100%;
        height: 100%;
        cursor: pointer;
        -webkit-transition: -webkit-transform 0.4s;
        -moz-transition: -moz-transform 0.4s;
        -o-transition: -o-transform 0.4s;
        transition: transform 0.4s;
        -webkit-transform-style: preserve-3d;
        -moz-transform-style: preserve-3d;
        -o-transform-style: preserve-3d;
        transform-style: preserve-3d;

      }
      .card.flipped {
        -webkit-transform: rotateY( 180deg );
        -moz-transform: rotateY( 180deg );
        -o-transform: rotateY( 180deg );
        transform: rotateY( 180deg );
      }
      
      .card .front,
      .card .back {
        padding-top: 20px;
        padding-bottom: 20px;    
        display: block;
        height: 100%;
        width: 100%;
        color: white;
        text-align: center;
        font-size: 1em;
        position: absolute;
        -webkit-backface-visibility: hidden;
        -moz-backface-visibility: hidden;
        -o-backface-visibility: hidden;
        backface-visibility: hidden;    
      //  box-shadow: 3px 5px 20px 2px rgba(0, 0, 0, 0.4);
      }
      .card .front {
        border-radius: 10px;
        background-color: rgba(0, 0, 0, 0.3);
       // backdrop-filter : blur(5px);
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;

        text{
            font-size: 2rem;
            font-family: Avenir;
            font-weight: 600;
        }
        .Titulo{
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          

          label{
            min-width: 50vw;
            display: flex;
            justify-content: space-around;
            align-items: center;
            font-size: 1.5rem;
            hr{
              min-width: 50px;
              max-width: 50px;
              border:1px solid rgba(255, 255, 255, 0.3);
            }      
          }
          .promo{
            color: #D79D69;
            display: flex;
            font-size: 2.8rem;
            font-family: Avegra-Regular;
            text-transform: uppercase;
          }
          .Subtitulo{
            font-size: 2.5rem;
            text-transform: uppercase;
            color: #FFF;
            font-family: GreycliffCF-Medium;
          }
          .medio{
            font-size: 1.5rem;
            font-family: GreycliffCF-Light;
          }

        }
        .formulario{
            min-width: 70vw;
            max-width: 70vw;
            min-height: 30vh;
            max-height: 30vh;
            margin: 0px;
            padding: 0px;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            input, label{
                margin: 0px;
                padding: 0px;
                min-width: 45vw;
                min-height: 30px;
                font-size: 1.2rem;
                display: flex;                
                border-radius: 5px;
                padding-left: 10px;
                border: none;
                                
            }
            label{
                padding-right: 10px;
                
                min-width: 60vw;
                max-width: 60vw;
                justify-content: center;
                margin-top: 15px;
                font-family: GreycliffCF-Medium;
            }
            input{
                min-width: 60vw;
                max-width: 60vw;
                max-height: 40px;
                font-family: GreycliffCF-Medium;
            }
        }
        .BotonIngreso{
            border: none;
            background-color: #8E7144;
            letter-spacing: 0.3em;
            color: #FFF;
            margin: 0px;
            padding: 0px;
            min-height: 40px;
            min-width: 50vw;
            max-width: 50vw;
            max-height: 40px;
            font-family: GreycliffCF-Medium;
            border-radius: 0px;
            font-size: 1.2rem;
            min-width: 62vw;
            padding-left: 2vw;
            padding-right: 2vw;
        }
        .mensajes{
          display:flex;
          justify-content: center;
          align-items: center;
          max-width: 50vw;
          color:  #D79D69;
          font-family: GreycliffCF-Medium;
        }
      }
      
      .card .back {
        border-radius: 10px;
        background-color: rgba(0, 0, 0, 0.3);       
        backdrop-filter : blur(5px);
        -webkit-transform: rotateY( 180deg );
        -moz-transform: rotateY( 180deg );
        -o-transform: rotateY( 180deg );
        transform: rotateY( 180deg );

        display: flex;
        flex-direction: column;
        justify-content: space-around;

        .fondoContainer{
          min-width: 60vw;
          min-height: 70vh;
          background-image: url('../components/registro_landing_qr_Mesa\ de\ trabajo\ 1.svg');
          background-size: 100vw;
          background-position: center;
          display: none;
          justify-content: center;
          align-items: center;
          .boton{
            margin-top: 150px;
            min-width: 60vw;
            min-height: 100px;
          }          
        }    
      }                
}